(function( $ ) {
	$.fn.sklonenie = function(number, xx1, xx2_xx4, xx5_xx0) {
		$this = $(this);
		var intNumber = parseInt(number);

		if (number >= 5 && number <= 20) {
			$this.html(xx5_xx0);
		}
		else if ((number - 1)%10 == 0) {
			$this.html(xx1);
		}
		else if ((number - 2)%10 == 0 || (number - 3)%10 == 0 || (number - 4)%10 == 0) {
			$this.html(xx2_xx4);
		}
		else {
			$this.html(xx5_xx0);
		}
	};
})(jQuery);